@font-face {
  font-family: 'Inter';
  src: url("../fonts/inter/Inter-Bold.woff2") format("woff2"), url("../fonts/inter/Inter-Bold.woff2") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Public Sans';
  src: url("../fonts/publickSans/PublicSans-Light.woff2") format("woff2"), url("../fonts/publickSans/PublicSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Public Sans';
  src: url("../fonts/publickSans/PublicSans-Bold.woff2") format("woff2"), url("../fonts/publickSans/PublicSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Public Sans';
  src: url("../fonts/publickSans/PublicSans-Regular.woff2") format("woff2"), url("../fonts/publickSans/PublicSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Quicksand';
  src: url("../fonts/quicksand-v30-latin-regular.woff2") format("woff2"), url("../fonts/quicksand-v30-latin-regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}

* {
  margin: 0;
  box-sizing: border-box;
}

::after,
::before {
  box-sizing: border-box;
}

.container {
  max-width: 1530px;
  min-width: 300px;
  margin: 0 auto;
  padding: 0 30px;
}

.body {
  font-family: 'Public Sans','Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.img-resonsive {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #0A1F32;
}

a:hover {
  color: #73CBE8;
}

.btn--color-orange {
  cursor: pointer;
  border-radius: 20px 6px;
  border: 2px solid #FF813A;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  background-color: #FF813A;
}

.btn--color-orange:hover {
  background-color: transparent;
  border: 2px solid #FF813A;
  border-radius: 20px 6px;
  color: #FF813A;
}

.btn--color-orange:active {
  color: #FFFFFF;
  background-color: #FF813A;
  border-radius: 20px 6px;
}

.header__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  position: relative;
}

.header__logo::before {
  content: '';
  position: absolute;
  height: 69px;
  width: 180px;
  left: -30px;
  top: -20px;
  background-color: #F9F9FB;
  border-radius: 0 0 30px;
}

.header__logo-img {
  display: flex;
}

.header__logo-svg {
  z-index: 10;
}

.header__menu {
  position: absolute;
  top: 49px;
  right: 0;
  transform: translateX(-100%);
  visibility: hidden;
  opacity: 0;
  z-index: 10;
  transition: all .3s ease;
}

.header__menu-body {
  background-color: #FFFFFF;
  padding: 40px 30px;
  width: 330px;
}

.header__menu-links {
  display: flex;
  flex-direction: column;
  padding-bottom: 18px;
  border-bottom: 2px solid rgba(0, 52, 88, 0.04);
}

.header__menu-link {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 29px;
  color: #0A1F32;
  margin-bottom: 20px;
}

.header__menu-link:hover path {
  stroke: #73CBE8;
}

.header__menu-svg {
  margin-left: 8px;
}

.header__menu.show {
  visibility: visible;
  transform: translateX(0%);
  opacity: 1;
}

.header__acount {
  display: flex;
  align-items: center;
}

.header__login {
  position: absolute;
  right: 0;
  top: 354px;
  transform: translateX(-100%);
  visibility: hidden;
  opacity: 0;
  z-index: 10;
  transition: all .3s ease;
}

.header__login-body {
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  padding: 40px 30px;
  width: 330px;
}

.header__login-link {
  display: flex;
  align-items: center;
  align-self: center;
  font-size: 18px;
  line-height: 29px;
  color: #0A1F32;
  margin-bottom: 20px;
}

.header__login-svg {
  margin-right: 10px;
}

.header__login.show {
  visibility: visible;
  transform: translateX(0%);
  opacity: 1;
}

.header__register-btn {
  font-family: 'Public Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  padding: 10px 35px;
  background-color: #FF813A;
}

.header__lang-link {
  display: flex;
  align-items: center;
  align-self: center;
  font-size: 20px;
  line-height: 29px;
  color: #0A1F32;
}

.header__lang-link:hover path {
  stroke: #73CBE8;
}

.header__lang-svg {
  margin-left: 8px;
}

.header__burger {
  position: relative;
  cursor: pointer;
  width: 41px;
  height: 20px;
  margin-left: 21px;
}

.header__burger-span {
  width: 41px;
  height: 3px;
  background-color: #003458;
  position: absolute;
  transition: all .3s ease;
}

.header__burger-span:hover path {
  stroke: #73CBE8;
}

.header__burger-span:nth-child(1) {
  top: 0;
}

.header__burger-span:nth-child(2) {
  top: 50%;
  right: 0;
  width: 33px;
  transform: translatey(-50%);
}

.header__burger-span:nth-child(3) {
  bottom: 0;
  right: 0;
  width: 22px;
}

.header__burger.show span:nth-child(1) {
  transform-origin: bottom left;
  transform: rotate(45deg);
  top: -9px;
}

.header__burger.show span:nth-child(2) {
  transform: scaleX(0);
}

.header__burger.show span:nth-child(3) {
  transform-origin: top left;
  transform: rotate(-45deg);
  width: 41px;
  top: 20px;
}

@media screen and (min-width: 800px) {
  .header__body {
    margin: 25px 0;
  }
  .header__logo-svg {
    width: 124px;
    height: 20px;
  }
  .header__logo::before {
    height: 79px;
    top: -25px;
    width: 255px;
  }
}

@media screen and (min-width: 1200px) {
  .header__body {
    margin: 18px 0;
  }
  .header__logo-svg {
    width: 104px;
    height: 17px;
  }
  .header__logo::before {
    left: -79px;
    height: 91px;
  }
  .header__menu {
    position: static;
    visibility: visible;
    opacity: 1;
    transform: translateX(0%);
  }
  .header__menu-body {
    background-color: transparent;
    padding: 0;
    width: 100%;
  }
  .header__menu-links {
    flex-direction: row;
    border: none;
    padding: 0;
  }
  .header__menu-link {
    white-space: nowrap;
    margin-bottom: 0;
    margin: 0 12px;
    font-size: 16px;
    line-height: 24px;
  }
  .header__menu-link:hover path {
    stroke: #73CBE8;
  }
  .header__lang-link {
    font-size: 16px;
  }
  .header__burger {
    display: none;
  }
  .header__login {
    position: static;
    transform: translateX(0%);
    visibility: visible;
    opacity: 1;
  }
  .header__login-body {
    flex-direction: row;
    background-color: transparent;
    padding: 0;
    width: 100%;
  }
  .header__login-link {
    display: flex;
    align-items: center;
    align-self: center;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 0;
  }
  .header__register-btn {
    font-size: 16px;
    line-height: 24px;
    margin: 0 15px;
  }
}

@media screen and (min-width: 1530px) {
  .header__logo::before {
    width: 470px;
    left: -294px;
  }
}

@media screen and (min-width: 1920px) {
  .header__menu-link {
    font-size: 18px;
    line-height: 29px;
  }
  .header__logo::before {
    width: 870px;
    left: -591px;
  }
  .header__logo-svg {
    width: 119px;
    height: 20px;
  }
  .header__login-link {
    font-size: 18px;
    line-height: 29px;
  }
  .header__register-btn {
    padding: 10px 35px;
    font-size: 18px;
    line-height: 29px;
  }
  .header__lang-link {
    font-size: 18px;
    line-height: 29px;
  }
}

.trustee {
  position: relative;
}

.trustee__pic {
  display: flex;
  background-color: #0A1F32;
  justify-content: center;
  object-fit: cover;
  overflow: hidden;
}

.trustee__pic::before {
  content: '';
  background: linear-gradient(90deg, #003458 5.21%, #003458 8.46%, rgba(0, 52, 88, 0) 99.06%);
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

@media screen and (min-width: 800px) {
  .trustee__pic:before {
    background: linear-gradient(90deg, #003458 5.21%, #003458 36.46%, rgba(0, 52, 88, 0) 99.06%);
    width: 55%;
  }
  .trustee__pic::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    background: linear-gradient(270deg, #003458 5.21%, rgba(0, 52, 88, 0) 100%);
    width: 55%;
  }
}

@media screen and (min-width: 1300px) {
  .trustee__pic::after {
    background: linear-gradient(270deg, #003458 16.21%, rgba(0, 52, 88, 0) 100%);
  }
}

@media screen and (min-width: 1500px) {
  .trustee__pic::after {
    background: linear-gradient(270deg, #003458 36.21%, rgba(0, 52, 88, 0) 100%);
  }
}

.footer {
  background-color: #1A1C26;
}

.footer__body {
  display: flex;
  flex-direction: column;
  padding: 40px 0;
}

.footer__info {
  margin-bottom: 40px;
  width: 270px;
}

.footer__info-logos {
  margin-bottom: 40px;
}

.footer__info-address {
  margin-bottom: 23px;
}

.footer__info-text {
  font-weight: 300;
  font-size: 15px;
  line-height: 26px;
  color: #687086;
}

.footer__sponsor {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 260px;
  margin-bottom: 40px;
}

.footer__sections-traders {
  border-top: 2px solid rgba(249, 249, 251, 0.1);
  border-bottom: 2px solid rgba(249, 249, 251, 0.1);
  padding: 15px 0;
}

.footer__sections-analytics {
  border-bottom: 2px solid rgba(249, 249, 251, 0.1);
  padding: 15px 0;
}

.footer__sections-education {
  border-bottom: 2px solid rgba(249, 249, 251, 0.1);
  padding: 15px 0;
}

.footer__sections-company {
  border-bottom: 2px solid rgba(249, 249, 251, 0.1);
  padding: 15px 0;
}

.footer__sections-documents {
  border-bottom: 2px solid rgba(249, 249, 251, 0.1);
  padding: 15px 0;
}

.footer__sections-list {
  list-style: none;
  position: absolute;
  padding: 0;
  visibility: hidden;
  opacity: 0;
  transition: all .2s ease;
}

.footer__sections-listTraders.show {
  position: static;
  top: 0;
  left: 0;
  visibility: visible;
  opacity: 1;
}

.footer__sections-listAnalytics.show {
  position: static;
  top: 0;
  left: 0;
  visibility: visible;
  opacity: 1;
}

.footer__sections-listEducation.show {
  position: static;
  top: 0;
  left: 0;
  visibility: visible;
  opacity: 1;
}

.footer__sections-listCompany.show {
  position: static;
  top: 0;
  left: 0;
  visibility: visible;
  opacity: 1;
}

.footer__sections-listDocuments.show {
  position: static;
  top: 0;
  left: 0;
  visibility: visible;
  opacity: 1;
}

.footer__sections-item {
  margin: 5px 0;
}

.footer__sections-link {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #F9F9FB;
  opacity: 0.6;
}

.footer__card {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__card-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #F9F9FB;
}

.footer__card-svg {
  margin-right: 2px;
  cursor: pointer;
}

.footer__card-svg:hover path {
  stroke: #73CBE8;
}

.footer__tm {
  align-self: center;
  margin-top: 30px;
}

.footer__tm-text {
  font-weight: 300;
  font-size: 15px;
  line-height: 26px;
  text-align: right;
  text-transform: uppercase;
  color: #687086;
  justify-content: center;
}

@media screen and (min-width: 800px) {
  .footer__info {
    display: grid;
    grid-template-columns: 400px 400px;
    grid-template-rows: min-content min-content;
  }
  .footer__info-logos {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .footer__info-address {
    grid-column: 1/2;
    grid-row: 2/-1;
  }
  .footer__sponsor {
    margin: 0;
    grid-column: 2/-1;
    grid-row: 2/-1;
  }
  .footer__sections {
    display: flex;
    justify-content: space-between;
    display: grid;
    grid-template-columns: 150px 150px 150px;
    grid-template-rows: min-content min-content;
    border-bottom: 2px solid rgba(249, 249, 251, 0.1);
    padding-bottom: 80px;
  }
  .footer__sections-traders {
    border: none;
    padding: 0;
  }
  .footer__sections-analytics {
    border: none;
    padding: 0;
  }
  .footer__sections-education {
    border: none;
    padding: 0;
    grid-column: 2/3;
    margin-top: -185px;
  }
  .footer__sections-company {
    border: none;
    padding: 0;
    grid-column: 1/2;
    grid-row: 2/3;
    margin-top: -97px;
  }
  .footer__sections-documents {
    border: none;
    padding: 0;
    grid-column: 3/-1;
    grid-row: 1/2;
  }
  .footer__sections-list {
    list-style: none;
    position: static;
    padding: 0;
    visibility: visible;
    opacity: 1;
  }
  .footer__sections-item {
    margin: 12px 0;
  }
  .footer__card {
    margin-bottom: 30px;
  }
  .footer__card-svg {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .footer__body {
    flex-direction: row;
    display: grid;
    grid-template-columns: 33.333% 66.666%;
    grid-template-rows: 1fr;
  }
  .footer__info {
    display: block;
  }
  .footer__sections {
    border-bottom: none;
    padding-bottom: 70px;
  }
  .footer__tm {
    display: flex;
    justify-content: center;
    width: 100%;
    border-top: 2px solid rgba(249, 249, 251, 0.1);
    grid-column: 1/-1;
    padding-top: 30px;
  }
}

@media screen and (min-width: 1920px) {
  .footer__sections {
    display: flex;
    padding-bottom: 60px;
  }
  .footer__sections-education {
    margin-top: 0;
  }
  .footer__sections-company {
    margin-top: 0;
  }
}
